.titleWrapper {
  display: block;
  position: relative;
  width: 100%;
}

.minus {
  content: url("./images/line.svg");
  width: 24px;
  height: 24px;
  position: absolute;
  right: 0;
}

.plus {
  content: url("./images/plus.svg");
  width: 24px;
  height: 24px;
  transform: rotate(90deg);
  transition-timing-function: ease-in;
  transition: all 1s;
  position: absolute;
  right: 0;
}

.panelClose {
  background: #e0e0e0;
  box-sizing: border-box;
  padding: 0.5rem 1rem 0 1rem;
  opacity: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  transition-timing-function: ease-out;
  transition: all 1s;
}

.panelOpen {
  overflow: hidden;
  background: #303030;
  color: #ffffff;
  padding: 1rem;
  margin-bottom: 1rem;
  box-sizing: border-box;
  opacity: 1;
  width: 100%;
  height: auto;
  transition-timing-function: ease-in;
  transition: all 1s;
}

.panel p {
  margin: 0;
}

.footer {
  text-align: center;
  height: 50px;
}
